.robot-wrap {
  margin-top: 20px;
}

.description {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
  padding-top: 20px;
  padding-bottom: 20px;
}

h2 {
  margin-bottom: 5px;
}

.input {
  display: inline-block;
  width: 400px;
  margin: 20px 0;
}

.scroll-box {
  position: relative;
  overflow-y: scroll;
  border-radius: 50px;
  margin: 0 25px;
  padding: 20px 0;
  height: 500px;
  border: 2px solid black;
  background: linear-gradient(
    90deg,
    rgba(123, 123, 244, 1) 0%,
    rgba(28, 177, 196, 1) 100%
  );
}

.container {
  max-width: 1088px;
  margin: 0 auto;
  padding: 50px 0;
}

@font-face {
  font-family: 'sega';
  src: url(/static/media/SEGA.82e09f78.TTF);
}

h1 {
  font-family: 'sega', 'Franklin Gothic Medium', 'Arial Narrow', Arial,
    sans-serif;
  color: #0ccac4;
  font-weight: 200;
  font-size: 3.2rem;
}

body,
body * {
  margin: 0;
  padding: 0;
}

body {
  height: 100%;
  font-family: sans-serif;
  background: linear-gradient(
    145deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(9, 9, 121, 1) 35%,
    rgba(0, 212, 255, 1) 100%
  );
  background-attachment: fixed;
}

